.chart-container {
  position: relative;
  font-family: Arial, sans-serif;
}

.legend {
  position: absolute;
  top: 120px;
  right: 900px; /* Moved closer to the streamgraph */
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 5px;
}

.legend-color {
  width: 16px;
  height: 16px;
  border-radius: 50%;
}

.tooltip {
  position: absolute;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}

.bar {
  stroke-width: 0;
}

